<template lang='html'>
  <div style='height: 90vh'>
      Component goes here
  </div>
</template>

<script>
import WebDataRocks from 'webdatarocks' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007'

export default {
  name: 'penelitian',
  mounted() {
    axios.get(host + '/api/penelitian').then(response => {
      console.log(response.data)
      this.webdatarocks = new WebDataRocks({
        report: {
          dataSource: { data: response.data },
          options: {
            grid: {
              type: 'flat',
              showTotals: false,
              showGrandTotals: false
            }
          }
        },
        container: this.$el,
        toolbar: true
      })
    }).catch((err) => {
      console.log(err)
    })
  }
}
</script>

<style scoped>
@import '~webdatarocks/webdatarocks.min.css'
</style>



