<template lang='html'>
  <div class='wrapper-jexcel'>
    <br>
    <div id='dropdown'>
        Pilih Departemen/Fakultas:
        <select name="satker" @change="onChange" class="form-control">
            <option v-for="option in options" v-bind:value="option.id" >{{ option.name }}</option>
        </select>
    </div>
    <br />
    <div id='doc-title'>
        <img id='logo' src="/static/img/Logo ITS.png">
        <div id='title'>
          <h1>Indikator Kontrak Kinerja 2020</h1>
          <h3> {{deptname}} </h3>
        </div>    
    </div>
    <div id='spreadsheet' ref='spreadsheet' style='padding-left: 1.2em;'></div>
  </div>
</template>

<script>
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007'

export default {
  name: 'jexcel',
  data() {
    return {
      options: {},
      id_satker: null,
      deptname: null,
      konkin: [
        {
          'aspek': null,
          'komponen': null,
          'Master_nama': null,
          'bobot': null,
          'target': null,
          'capaian': null,
          'last_update': null
        }
      ]
    }
  },
  computed: {
    jExcelOptions() {
      return {
        data: this.konkin,
        columns: [
          { type: 'text', title: 'Aspek', width: '100px', name: 'aspek' },
          { type: 'text', title: 'komponen', width: '180px', name: 'komponen' },
          { type: 'text', title: 'Indikator', width: '350px', name: 'Master_nama' },
          { type: 'numeric', title: 'bobot', width: '120px', name: 'bobot' },
          { type: 'numeric', title: 'Target', width: '110px', name: 'target' },
          { type: 'numeric', title: 'Capaian', width: '130px', name: 'capaian' },
          { type: 'text', title: 'last_update', width: '180px', name: 'last_update' }
        ],
        responsive: true,
        tableoverflow: true,
        wordWrap: true,
        editable: false
      }
    }
  },
  methods: {
    onChange(event) {
      this.id_satker = event.target.value
      axios.get(host + '/api/nama-satker/' + this.id_satker).then(response => {
        this.deptname = response.data[0].name
      }).catch((err) => {
        console.log(err)
      })
      axios.get(host + '/api/indikator/' + this.id_satker).then(response => {
        console.log('OnChange() called')
        this.konkin = response.data
      }).catch((err) => {
        console.log(err)
      })
    },
    load() {
      this.deptname = JSON.parse(localStorage.getItem('user'))
      this.id_satker = localStorage.getItem('id_satker')

      axios.get(host + '/api/konkin-list/' + this.id_satker).then(response => {
        this.options = response.data
      })
      axios.get(host + '/api/indikator/' + this.id_satker).then(response => {
        console.log(response)
        var valuesOnly = response.data
        this.konkin = valuesOnly
        console.log(this.konkin)
      }).catch((err) => {
        console.log(err)
      })
    }
  },
  mounted() {
    this.load()
  },
  watch: {
    konkin () {
      document.getElementById('spreadsheet').innerHTML = ''
      const jExcelObj = jexcel(this.$refs['spreadsheet'], this.jExcelOptions)
      Object.assign(this, { jExcelObj })
    }

  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
#dropdown {
  position: relative;
  width: 600px;
  height: 40px;
  left: 25%;
}

#logo{
  width: 20%;
  float: left;
}

#title {
  position: relative;
  width: 470px;
  height: 60px;
  float: right;
  margin-top: 15px;
}

h3, h1 {
  margin-top: 5px;
  margin-bottom: 2px;
}

#doc-title {
  width: 610px;
  height: 120px;
  padding-left: 1.2em;
}

</style>



