<template lang='html'>
  <div class='wrapper-jexcel'>
    <br>
    <input
      class='btn-primary'
      type='button'
      value='Add new row'
      @click='jExcelObj.insertRow()'
    />
    <input
      class='btn-primary'
      type='button'
      value='Delete row'
      @click='jExcelObj.deleteRow()'
    />
    <br />
    <div id='spreadsheet' ref='spreadsheet'></div>
  </div>
</template>

<script>
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007'

export default {
  name: 'jexcel',
  data() {
    return {
      data: [
        {
          'id': 0,
          'aspek': null,
          'komponen_aspek': null
        }
      ]
    }
  },
  computed: {
    jExcelOptions() {
      return {
        data: this.data,
        columns: [
          { type: 'hidden', title: 'ID', width: '120px', name: 'id' },
          { type: 'text', title: 'Aspek', width: '200px', name: 'aspek' },
          { type: 'text', title: 'Komponen Aspek', width: '240px', name: 'komponen_aspek' }
        ],
        onchange: this.changed,
        oninsertrow: this.insertedRow,
        ondeleterow: this.deleteRow,
        tableoverflow: true,
        wordWrap: true,
        pagination: 30,
        responsive: true
      }
    }
  },
  methods: {
    load() {
      console.log('load')
      console.log(host + '/api/aspek')
      axios.get(host + '/api/aspek').then(response => {
        console.log(response)
        var valuesOnly = response.data
        this.data = valuesOnly
        console.log(this.data)
      }).catch((err) => {
        console.log(err)
      })
    },
    changed(instance, cell, x, y, value) {
      x = parseInt(x)
      y = parseInt(y)

      console.log('x: ' + x + '\ny: ' + y)

      var id = y
      var data = {}
      axios.get(host + '/api/aspek').then((response) => {
        data = Object.values(response.data[id])

        data[x] = value
        console.log(data)
        axios({
          method: 'put',
          url: host + '/api/aspek/' + data[0],
          data: {
            'id': data[0],
            'aspek': data[1],
            'komponen_aspek': data[2]
          }
        }).then((response) => {
          console.log(response.data)
        })
      })
    },
    insertedRow(instance) {
      axios({
        method: 'post',
        url: host + '/api/aspek/',
        data: {
          'aspek': null
        }
      }).then((response) => {
        console.log(response.data)
      })
    },
    deleteRow(instance, row) {
      console.log(row)
      axios.get(host + '/api/aspek/').then(res => {
        console.log(res.data[row])
        var index = Object.values(res.data[row])
        // console.log(index)
        axios.delete(host + '/api/aspek/' + index[0])
      })
    }
  },
  mounted() {
    this.load()
  },
  watch: {
    data () {
      const jExcelObj = jexcel(this.$refs['spreadsheet'], this.jExcelOptions)
      Object.assign(this, { jExcelObj })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



