<template lang='html'>
  <div class='wrapper-jexcel'>
    <br>
    <div id='spreadsheet' ref='spreadsheet'>
    </div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/indikator-satker/log').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          editable: false,
          columns: [
            { type: 'text', title: 'Periode', width: '120px', readOnly: true, name: 'id_periode' },
            { type: 'text', title: 'ID Master', width: '120px', readOnly: true, name: 'id_master' },
            { type: 'text', title: 'ID Satuan Kerja', width: '230px', readOnly: true, name: 'id_satker' },
            { type: 'numeric', title: 'Capaian', width: '120px', readOnly: true },
            { type: 'text', title: 'Create Date', width: '200px', readOnly: true }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



