<template lang='html'>
  <div class='wrapper-jexcel'>
    <div id='spreadsheet' ref='spreadsheet'>
      <input class='btn-primary' type='button' value='Add new row' @click='() => spreadsheet.insertRow()' />
      <input class='btn-primary' type='button' value='Delete row' @click='() => spreadsheet.deleteRow()' />
    </div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/satker/').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          onchange: this.changed,
          oninsertrow: this.insertrow,
          ondeleterow: this.deleterow,
          allowToolbar: true,
          responsive: true,
          tableoverflow: true,
          wordWrap: true,
          pagination: 30,
          columns: [
            { type: 'text', title: 'ID', width: '180px', readOnly: true, name: 'id_satker' },
            { type: 'dropdown', title: 'Jenis Satuan Kerja', width: '160px', url: host + 'api/jenis-satker-dropdown/', name: 'id_jns_satker', autocomplete: true },
            { type: 'text', title: 'Induk Satuan Kerja', width: '180px', autocomplete: true, name: 'id_induk_satker' },
            { type: 'text', title: 'Nama', width: '120px', name: 'nama' },
            { type: 'text', title: 'E-Mail', width: '120px', name: 'email' },
            { type: 'numeric', title: 'Level Unit', width: '85px', name: 'level_unit' },
            { type: 'text', title: 'Create Date', width: '180px', name: 'create_date', readOnly: true },
            { type: 'text', title: 'Last Update', width: '180px', name: 'last_update', readOnly: true },
            { type: 'text', title: 'Expired Date', width: '180px', name: 'expired_date' }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    },
    changed(instance, cell, columns, row, value) {
      axios.get(host + 'api/satker/').then(response => {
        console.log(columns)
        var data = Object.values(response.data[row])
        data[columns] = value
        console.log(data)
        axios
          .put(host + 'api/satker/' + data[0], {
            id_jns_satker: data[1],
            id_induk_satker: data[2],
            nama: data[3],
            email: data[4],
            level_unit: data[5],
            create_date: data[6],
            last_update: data[7],
            expired_date: data[8]
          })
          .then(response => {
            console.log('Query update berhasil')
          })
          .catch(err => {
            console.log(err)
          })
      })
    },
    insertrow(instance) {
      axios({
        method: 'post',
        url: host + 'api/satker/',
        data: {
          id_jns_satker: null,
          id_induk_satker: 'EB863F41-40E6-4EBE-BF0B-D30F8037E4A7',
          nama: 'new',
          email: '',
          expired_date: ''
        }
      })
        .then(response => {
          console.log('Query insert berhasil')
        })
        .catch(err => {
          console.log(err)
        })
    },
    deleterow(instance, row) {
      axios.get(host + 'api/satker/').then(response => {
        var index = Object.values(response.data[row])
        console.log('Index: ' + index)
        console.log('Deleting row: ' + row)
        axios.delete(host + 'api/satker/' + index[0])
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



