<template lang='html'>
  <div class='wrapper-jexcel'>
    <br>
    <div id='spreadsheet' ref='spreadsheet'>
      <input class='btn-primary' type='button' value='Add new row' @click='() => spreadsheet.insertRow()' />
      <input class='btn-primary' type='button' value='Delete row' @click='() => spreadsheet.deleteRow()' />
    </div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/indikator-satker/').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          onchange: this.changed,
          oninsertrow: this.insertrow,
          ondeleterow: this.deleterow,
          pagination: 25,
          allowToolbar: true,
          responsive: true,
          tableoverflow: true,
          wordWrap: true,
          allowManualInsertRow: false,
          columns: [
            { type: 'dropdown', title: 'Periode', width: '120px', url: host + 'api/periode-dropdown/', autocomplete: true },
            { type: 'dropdown', title: 'Indikator', width: '250px', url: host + 'api/master-indikator-dropdown/', autocomplete: true },
            { type: 'text', title: 'Satuan Kerja', width: '270px', autocomplete: true },
            { type: 'numeric', title: 'Bobot', width: '120px' },
            { type: 'numeric', title: 'Target', width: '120px' },
            { type: 'numeric', title: 'Capaian', width: '120px' },
            { type: 'text', title: 'Last Update', width: '120px' }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    },
    changed(instance, cell, columns, row, value) {
      axios.get(host + 'api/indikator-satker/').then(response => {
        if (Number(columns) === 2) {
          axios.get(host + 'api/satker/' + value).then(response => {
            var id = Object.values(response.data[row])
            console.log('id: ' + id[0] + ' ' + id[1])
            value = id[1]
          })
        }
        var old = Object.values(response.data[row])
        var newData = Object.values(response.data[row])
        console.log('old: ' + old[0] + ' ' + old[1] + ' ' + old[2])
        console.log('new: ' + newData[0] + ' ' + newData[1] + ' ' + newData[2])
        newData[columns] = value
        axios.put(host + 'api/indikator-satker/' + old[0] + '&' + old[1] + '&' + old[2], {
          id_periode: newData[0],
          id_master: newData[1],
          id_satker: newData[2],
          bobot: newData[3],
          target: newData[4],
          capaian: newData[5],
          last_update: newData[6]
        })
          .then(response => {
            console.log(response.data)
          })
          .catch(err => {
            console.log(err)
          })
      })
    },
    insertrow(instance) {
      console.log('inserted row')
      axios({
        method: 'post',
        url: host + 'api/indikator-satker',
        data: {
          id_periode: 1,
          id_master: 2,
          id_satker: '4D589734-7E1D-41C4-9A56-5A0CC536AC60'
        }
      })
        .then(response => {
          console.log(response.data)
          console.log('new row')
        })
        .catch(err => {
          console.log(err)
        })
    },
    deleterow(instance, row) {
      axios.get(host + 'api/indikator-satker/').then(response => {
        var index = Object.values(response.data[row])
        axios.delete(host + 'api/indikator-satker/' + index[0] + '&' + index[1] + '&' + index[2])
        .catch(err => {
          console.log(err)
        })
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



