<template>
  <div>
    <div id="spreadsheet" class="container">
      <input class="btn-primary" type="button" value="Add new row" @click="() => spreadsheet.insertRow()" />
      <input class="btn-primary" type="button" value="Delete row" @click="() => spreadsheet.deleteRow()" />
    </div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

var insertrow = function(instance) {
  axios({
    method: 'post',
    url: host + 'api/capaian/',
    data: {
      id_satker: '4D589734-7E1D-41C4-9A56-5A0CC536AC60',
      id_datadasar: 1,
      waktu: '1970-00-00T00:00:00.000Z',
      capaian: 0.0
    }
  })
    .then(response => {
      console.log(response.data)
    })
    .catch(err => {
      console.log(err)
    })
}

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/capaian/').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          onchange: this.changed,
          oninsertrow: insertrow,
          ondeleterow: this.deleterow,
          allowToolbar: true,
          responsive: true,
          tableoverflow: true,
          wordWrap: true,
          columns: [
            { type: 'text', title: 'Satuan Kerja', width: '300px', autocomplete: true },
            { type: 'dropdown', title: 'Data Dasar', width: '320px', url: host + 'api/dasar-dropdown/', autocomplete: true },
            { type: 'text', title: 'Waktu', width: '200px', name: 'waktu' },
            { type: 'numeric', title: 'Capaian', width: '120px' }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    },
    changed(instance, cell, columns, row, value) {
      console.log(value + ' ' + row + ' ' + columns)
      axios.get(host + 'api/capaian/').then(response => {
        var old = Object.values(response.data[row])
        var index = Object.values(response.data[row])
        index[columns] = value
        console.log('old: ' + old[0] + ' ' + old[1])
        console.log('new: ' + index[0] + ' ' + index[1])
        axios.put(host + 'api/capaian/' + old[0] + '&' + old[1], {
          id_satker: index[0],
          id_datadasar: index[1],
          waktu: index[2],
          capaian: index[3]
        })
          .then(response => {
            console.log(response.data)
          })
          .catch(err => {
            console.log(err)
          })
      })
    },
    deleterow(instance, row) {
      axios.get(host + 'api/capaian/').then(response => {
        var index = Object.values(response.data[row])
        console.log('Index: ' + index)
        console.log('Deleting row: ' + row)
        axios.delete(host + 'api/capaian/' + index[0] + '&' + index[1])
        .catch(err => {
          console.log(err)
        })
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



