import DashView from './components/Dash.vue'
import LoginView from './components/Login.vue'
import NotFoundView from './components/404.vue'

// Import Views - Dash
import DashboardView from './components/views/Dashboard.vue'
import ExcelView from './components/views/Excel.vue'
import DataDasarView from './components/views/DataDasar.vue'
import PeriodeView from './components/views/Periode.vue'
import JenisSatkerView from './components/views/JenisSatker.vue'
import CapaianUnitView from './components/views/CapaianUnit.vue'
import MasterIndikatorView from './components/views/MasterIndikator.vue'
import SatkerView from './components/views/SatuanKerja.vue'
import IndikatorSatkerView from './components/views/Indikator_Satker.vue'
import IndikatorSatkerLogView from './components/views/Indikator_Satker_log.vue'
import IndikatorPeriodeView from './components/views/Indikator_Periode.vue'
import AspekView from './components/views/Aspek.vue'
import DosenView from './components/views/Dosen.vue'
import PublikasiView from './components/views/Publikasi.vue'
import PenelitianView from './components/views/Penelitian.vue'
import AbmasView from './components/views/Abmas.vue'
import KonKinView from './components/views/Konkin.vue'

// Routes
const routes = [
  {
    path: '/login',
    component: LoginView,
    meta: {onlyNonAuth: true}
  },
  {
    path: '/',
    component: DashView,
    children: [
      {
        path: 'dashboard',
        alias: '',
        component: DashboardView,
        name: 'Dashboard',
        meta: {description: 'Halaman Dashboard'}
      }, {
        path: 'konkin',
        component: KonKinView,
        name: 'Kontrak Kinerja',
        meta: {description: 'Kontrak Kinerja', requiresAuth: true}
      }, {
        path: 'excel',
        component: ExcelView,
        name: 'Excel',
        meta: {description: 'Simple and advance table in CoPilot but with jexcels'}
      }, {
        path: 'abmas',
        component: AbmasView,
        name: 'Abmas',
        meta: {description: 'Tabel abmas dalam WebDataRocks', requiresAuth: true}
      }, {
        path: 'dosen',
        component: DosenView,
        name: 'Dosen',
        meta: {description: 'Tabel dosen dalam WebDataRocks', requiresAuth: true}
      }, {
        path: 'penelitian',
        component: PenelitianView,
        name: 'Penelitian',
        meta: {description: 'Tabel penelitian dalam WebDataRocks', requiresAuth: true}
      }, {
        path: 'publikasi',
        component: PublikasiView,
        name: 'Publikasi',
        meta: {description: 'Tabel publikasi dalam WebDataRocks', requiresAuth: true}
      }, {
        path: 'aspek',
        component: AspekView,
        name: 'Aspek',
        meta: {description: 'Tabel Aspek dalam Jexcel', requiresAuth: true}
      }, {
        path: 'datadasar',
        component: DataDasarView,
        name: 'DataDasar',
        meta: {description: 'Tabel DataDasar dalam Jexcel', requiresAuth: true}
      }, {
        path: 'satker',
        component: SatkerView,
        name: 'Satuan Kerja',
        meta: {description: 'Tabel Satuan Kerja dalam Jexcel', requiresAuth: true}
      }, {
        path: 'periode',
        component: PeriodeView,
        name: 'Periode',
        meta: {description: 'Tabel Periode dalam Jexcel', requiresAuth: true}
      }, {
        path: 'capaianunit',
        component: CapaianUnitView,
        name: 'CapaianUnit',
        meta: {description: 'Tabel CapaianUnit dalam Jexcel', requiresAuth: true}
      }, {
        path: 'indikator-periode',
        component: IndikatorPeriodeView,
        name: 'Indikator Periode',
        meta: {description: 'Tabel Indikator Periode dalam Jexcel', requiresAuth: true}
      }, {
        path: 'indikator-satker-log',
        component: IndikatorSatkerLogView,
        name: 'Log Indikator Satuan Kerja',
        meta: {description: 'Tabel Log Indikator Satuan Kerja dalam Jexcel', requiresAuth: true}
      }, {
        path: 'indikator-satker',
        component: IndikatorSatkerView,
        name: 'Indikator Satuan Kerja',
        meta: {description: 'Tabel Indikator Satuan Kerja dalam Jexcel', requiresAuth: true}
      }, {
        path: 'master-indikator',
        component: MasterIndikatorView,
        name: 'Master Indikator',
        meta: {description: 'Tabel Master Indikator dalam Jexcel', requiresAuth: true}
      }, {
        path: 'jenis-satker',
        component: JenisSatkerView,
        name: 'Jenis Satuan Kerja',
        meta: {description: 'Tabel Jenis Satuan Kerja dalam Jexcel', requiresAuth: true}
      }
    ]
  }, {
    // not found handler
    path: '*',
    component: NotFoundView
  }
]

export default routes



