<template lang='html'>
  <div class='wrapper-jexcel'>
    <br>
    <div id='spreadsheet' ref='spreadsheet'>
      <input class='btn-primary' type='button' value='Add new row' @click='() => spreadsheet.insertRow()' />
      <input class='btn-primary' type='button' value='Delete row' @click='() => spreadsheet.deleteRow()' />
    </div>
  </div>
</template>


<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/indikator-periode/').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          onchange: this.changed,
          oninsertrow: this.insertrow,
          ondeleterow: this.deleterow,
          allowToolbar: true,
          responsive: true,
          wordWrap: true,
          pagination: 25,
          columns: [
            { type: 'dropdown', title: 'Indikator', width: '650px', url: host + 'api/master-indikator-dropdown/', autocomplete: true },
            { type: 'dropdown', title: 'Periode', width: '120px', url: host + 'api/periode-dropdown/', autocomplete: true },
            { type: 'numeric', title: 'Bobot', width: '120px' }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    },
    changed(instance, cell, columns, row, value) {
      axios.get(host + 'api/indikator-periode/').then(response => {
        var old = Object.values(response.data[row])
        var newData = Object.values(response.data[row])
        newData[columns] = value
        axios.put(host + 'api/indikator-periode/' + old[0] + '&' + old[1], {
          id_master: newData[0],
          id_periode: newData[1],
          bobot: newData[2]
        })
          .then(response => {
            console.log(response.data)
          })
          .catch(err => {
            console.log(err)
          })
      })
    },
    insertrow(instance) {
      axios({
        method: 'post',
        url: host + 'api/indikator-periode/',
        data: {
          id_periode: 3,
          id_master: 2,
          waktu: null,
          capaian: 0.0
        }
      })
        .then(response => {
          console.log(response.data)
        })
        .catch(err => {
          console.log(err)
        })
    },
    deleterow(instance, row) {
      axios.get(host + 'api/indikator-periode/').then(response => {
        var index = Object.values(response.data[row])
        console.log('Index: ' + index)
        console.log('Deleting row: ' + row)
        axios.delete(host + 'api/indikator-periode/' + index[0] + '&' + index[1])
        .catch(err => {
          console.log(err)
        })
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



