<template lang='html'>
  <div class='wrapper-jexcel'>
    <br>
    <div id='spreadsheet' ref='spreadsheet'>
      <input class='btn-primary' type='button' value='Add new row' @click='() => spreadsheet.insertRow()' />
      <input class='btn-primary' type='button' value='Delete row' @click='() => spreadsheet.deleteRow()' />
    </div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/master-indikator/').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          onchange: this.changed,
          oninsertrow: this.insertrow,
          ondeleterow: this.deleterow,
          allowToolbar: true,
          responsive: true,
          tableoverflow: true,
          wordWrap: true,
          pagination: 10,
          columns: [
            { type: 'hidden', title: 'ID', width: '120px' },
            { type: 'dropdown', title: 'Aspek', width: '120px', url: host + 'api/aspek-dropdown/', autocomplete: true },
            { type: 'dropdown', title: 'Penyebut', width: '120px', url: host + 'api/dasar-dropdown/', autocomplete: true },
            { type: 'dropdown', title: 'Pembilang', width: '120px', url: host + 'api/dasar-dropdown/', autocomplete: true },
            { type: 'text', title: 'Nama', width: '120px', name: 'nama' },
            { type: 'text', title: 'Deskripsi', width: '170px', name: 'deskripsi' },
            { type: 'numeric', title: 'Default Bobot', width: '100px', name: 'default_bobot' },
            { type: 'text', title: 'Create Date', width: '130px', name: 'create_date', readOnly: true },
            { type: 'text', title: 'Last Update', width: '130px', name: 'last_update', readOnly: true },
            { type: 'text', title: 'Expired Date', width: '130px', name: 'expired_date' }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    },
    changed(instance, cell, columns, row, value) {
      console.log('row: ' + row + ' column: ' + columns)
      axios.get(host + 'api/master-indikator/').then(response => {
        var data = Object.values(response.data[row])
        data[columns] = value
        axios
          .put(host + 'api/master-indikator/' + data[0], {
            id: data[0],
            id_aspek: data[1],
            id_penyebut: data[2],
            id_pembilang: data[3],
            nama: data[4],
            deskripsi: data[5],
            default_bobot: data[6],
            create_date: data[7],
            last_update: data[8],
            expired_date: data[9]
          })
          .then(response => {
            console.log(response.data)
          })
      })
    },
    insertrow(instance) {
      axios({
        method: 'post',
        url: host + 'api/master-indikator/',
        data: {
          id_penyebut: null,
          id_pembilang: null,
          id_aspek: null,
          nama: ''
        }
      })
        .then(response => {
          console.log(response.data)
        })
        .catch(err => {
          console.log(err)
        })
    },
    deleterow(instance, row) {
      axios.get(host + 'api/master-indikator/').then(response => {
        var index = Object.values(response.data[row])
        axios.delete(host + 'api/master-indikator/' + index[0])
      })
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}
</style>



