<template lang='html'>
  <div class='wrapper-jexcel'>
    <div id='spreadsheet' ref='spreadsheet'>
      <input class='btn-primary' type='button' value='Add new row' @click='() => spreadsheet.insertRow()' />
      <input class='btn-primary' type='button' value='Delete row' @click='() => spreadsheet.deleteRow()' />
    </div>
  </div>
</template>

<script>
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://10.199.14.46:8007/'

export default {
  name: 'jexcel',
  mounted() {
    this.load()
  },
  methods: {
    load() {
      axios.get(host + 'api/periode/').then(res => {
        console.log(res.data)
        var options = {
          data: res.data,
          onchange: this.changed,
          oninsertrow: this.insertrow,
          ondeleterow: this.deleterow,
          allowToolbar: true,
          responsive: true,
          columns: [
            { type: 'hidden', title: 'ID', width: '120px', name: 'id' },
            { type: 'text', title: 'Nama', width: '150px', name: 'nama' },
            { type: 'text', title: 'Create Date', width: '200px', name: 'create_date', readOnly: true },
            { type: 'text', title: 'Last Update', width: '200px', name: 'last_update', readOnly: true }
          ]
        }
        let spreadsheet = jexcel(this.$el, options)
        Object.assign(this, { spreadsheet })
      })
    },
    changed(instance, cell, columns, row, value) {
      axios.get(host + 'api/periode/').then(response => {
        var data = Object.values(response.data[row])
        data[columns] = value
        axios.put(host + 'api/periode/' + data[0], {
          nama: data[1],
          create_date: data[2],
          last_update: data[3]
        })
          .then(response => {
            console.log(response.data)
          })
      })
    },
    insertrow(instance) {
      axios({
        method: 'post',
        url: host + 'api/periode/',
        data: {
          nama: null
        }
      })
        .then(response => {
          console.log(response.data)
        })
        .catch(err => {
          console.log(err)
        })
    },
    deleterow(instance, row) {
      axios.get(host + 'api/periode/').then(response => {
        var index = Object.values(response.data[row])
        axios.delete(host + 'api/periode/' + index[0])
      })
    }
  }
}
</script>



